"use client"

import { Button } from "@/components/ui/button"
import { useState, useEffect } from "react"
import Link from "next/link"
import { useLanguage } from "@/contexts/language-context"

export function Header() {
  const { t } = useLanguage()
  const [isAuthenticated, setIsAuthenticated] = useState(false)

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const response = await fetch("/api/user", { cache: "no-store" })
        setIsAuthenticated(response.ok)
      } catch {
        setIsAuthenticated(false)
      }
    }
    checkAuth()
  }, [])

  return (
    <header className="border-b-2 border-border/30 backdrop-blur-md bg-background/70 sticky top-0 z-50 shadow-sm animate-fade-in">
      <div className="container mx-auto px-4 h-16 flex items-center justify-between">
        <Link href="/" className="flex items-center gap-3 group">
          <div className="w-9 h-9 rounded-lg flex items-center justify-center shadow-md group-hover:shadow-lg group-hover:scale-105 transition-all duration-300 overflow-hidden bg-[#8C6CFA]">
            {/* Лого "V" на фоне как в картинке — круглая форма, белая буква, фиолетовый фон */}
            <span className="text-white text-xl font-bold select-none" style={{
              fontFamily: "Inter, sans-serif",
              textShadow: "0 1px 7px #7055cf"
            }}>
              V
            </span>
          </div>
          <h1 className="text-xl font-bold text-foreground tracking-tight transition-colors duration-200 group-hover:text-primary">VDS_HUB</h1>
        </Link>

        <div className="flex items-center gap-3">
          {isAuthenticated ? (
            <Link href="/dashboard">
              <Button size="sm" className="bg-primary hover:bg-primary/90 font-semibold shadow-sm hover:shadow-md transition-transform duration-200 hover:-translate-y-0.5">
                {t("header.dashboard")}
              </Button>
            </Link>
          ) : (
            <>
              <Link href="/login">
                <Button variant="outline" size="sm" className="font-semibold bg-transparent transition-transform duration-200 hover:-translate-y-0.5">
                  {t("header.login")}
                </Button>
              </Link>
              <Link href="/register">
                <Button size="sm" className="bg-primary hover:bg-primary/90 font-semibold shadow-sm hover:shadow-md transition-transform duration-200 hover:-translate-y-0.5">
                  {t("header.register")}
                </Button>
              </Link>
            </>
          )}
        </div>
      </div>
    </header>
  )
}
